.cardList {
  display: flex;
  gap: 32px;
  justify-content: center;
  flex-wrap: wrap;

  &__item {
    max-width: 400px;
    min-height: 400px;
  }
}


@media (min-width: 768px) {
  .cardList {
    justify-content: space-around;

    &__item {
      width: calc(50% - 32px);
    }
  }
}