@import url(color.css);

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
}

html, body {
  height: 100%;
}

body {
  background: var(--bg-blue-800);
  font-family: "Roboto", sans-serif;
  color: var(--c-white);
}

#root {
  height: 100%;
}

@media (min-width: 768px) {
  root {
    max-width: 768px;
  }
}

